@import url(https://rsms.me/inter/inter.css);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  font-family: "Inter", sans-serif;
  background: #ffffff;
  color: #111111;
}

@supports (font-variation-settings: normal) {
  html {
    font-family: "Inter var", sans-serif;
  }
}

:root {
  --fontSize: calc(12px + 0.698vw); /* The character count on */
  --lineHeight: calc(var(--fontSize) * 1.4);
  --letterSpacing: calc(var(--fontSize) * 0.0015);
  --fontWeight: 300;
}

img {
  image-rendering: crisp-edges;
  -ms-interpolation-mode: nearest-neighbor;
      image-rendering: pixelated;
}

.app {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.library {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.row {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5vh;
}

.song {
  width: 30%;
  cursor: pointer;
}

h2 {
  font-size: calc(var(--fontSize) * 1.75);
  letter-spacing: var(--letterSpacing);
}

h4 {
  font-size: calc(var(--fontSize) * 1);
  letter-spacing: var(--letterSpacing);
  padding-top: 2vh;
}

p {
  font-size: calc(var(--fontSize) * 0.75);
  letter-spacing: var(--letterSpacing);
}

a {
  color: black;
  font-weight: 600;
}

.image {
  width: 85%;
  border-radius: 0.25vw;
  box-shadow: 0px 0.2vw 5.2vw rgba(0, 0, 0, 0.05);
}

.header-text {
  width: 85%;
  height: 20vh;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  margin: 2% 0;
}

@media (max-width: 900px) {
  .row {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 2vh;
  }

  .song {
    width: 90%;
    padding: 5vh;
  }

  .image {
    width: 100%;
  }

  h2 {
    font-size: calc(var(--fontSize) * 2);
  }

  p {
    font-size: calc(var(--fontSize) * 1);
  }

  h4 {
    font-size: calc(var(--fontSize) * 1.25);
  }
}

@media (max-width: 540px) {
  .row {
    padding: 1vh;
  }

  .song {
    width: 95%;
    padding: 2vh;
  }

  h2 {
    font-size: calc(var(--fontSize) * 1.75);
  }

  p {
    font-size: calc(var(--fontSize) * 0.9);
  }

  h4 {
    font-size: calc(var(--fontSize) * 1.2);
  }

  .header-text {
    height: 22vh;
    margin: 2% 0;
  }
}

@media (max-width: 400px) {
  h2 {
    font-size: calc(var(--fontSize) * 1.5);
  }

  p {
    font-size: calc(var(--fontSize) * 0.8);
  }
}

.wrapper {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.song-page {
  width: 100%;
  height: 85vh;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

audio {
  display: block;
  width: 40vw;
}

.song-title-page {
  font-size: calc(var(--fontSize) * 1.5);
  letter-spacing: var(--letterSpacing);
  font-family: "Inter", sans-serif;
}

.page-image {
  width: 50%;
  border-radius: 0.5vw;
  box-shadow: 0px 0.2vw 5.2vw rgba(0, 0, 0, 0.2);
}

.back {
  width: 1%;
  cursor: pointer;
  margin: 2.5% auto 2.5% 3.5%;
}

@media (max-width: 960px) {
  .page-image {
    width: 75%;
  }

  .song-page {
    height: 85vh;
  }

  audio {
    display: block;
    width: 60vw;
  }

  .back {
    width: 1.5%;
  }
}

@media (max-width: 760px) {
  .back {
    width: 2%;
  }
}

@media (max-width: 540px) {
  .page-image {
    width: 85%;
  }

  body {
    overflow-x: hidden;
  }

  audio {
    width: 75vw;
  }

  .song-page {
    height: 65vh;
  }

  .back {
    width: 3%;
    margin: 0 auto 20% 7%;
  }
}

